// --sb-content-block-h2-font-weight
// --sb-content-block-font-size-medium
// --sb-content-block-code-color

.sb-content-block {
  font-weight: var(--sb-content-block-font-weight, var(--sbt-font-weight-40));

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: var(
      --sb-content-block-header-color,
      var(--sbt-theme-tertiary-color-90)
    );
    font-family: var(
      --sb-content-block-header-font-family,
      var(--sbt-font-stack-alt)
    );
    font-weight: var(
      --sb-content-block-header-font-weight,
      var(--sbt-font-weight-70)
    );
    line-height: var(
      --sb-content-block-header-line-height,
      var(--sbt-line-height-25)
    );
    margin: 1rem 0;
  }

  a {
    color: var(--sb-content-block-a-color, var(--sbt-color-body-link));
    font-weight: var(
      --sb-content-block-a-font-weight,
      var(--sbt-font-weight-60)
    );

    &:hover {
      color: var(
        --sb-content-block-a-hover-color,
        var(--sbt-color-body-link-dark)
      );
    }
  }

  mark {
    background-color: var(
      --sb-content-block-mark-background-color,
      var(--sbt-purple-40)
    );
  }

  code {
    background-color: var(
      --sb-content-block-code-background-color,
      var(--sbt-theme-tertiary-color-10)
    );
    color: var(--sb-content-block-code-color, #e83e8c);
    padding: 0 0.25rem;
  }

  h1 {
    font-size: var(--sb-content-block-h1-font-size, var(--sbt-font-size-80));
  }

  h2 {
    font-size: var(--sb-content-block-h2-font-size, var(--sbt-font-size-70));
    font-weight: var(
      --sb-content-block-h2-font-weight,
      var(--sbt-font-weight-60)
    );
  }

  h3 {
    font-size: var(--sb-content-block-h3-font-size, var(--sbt-font-size-50));
  }

  h4 {
    font-size: var(--sb-content-block-h4-font-size, var(--sbt-font-size-35));
    font-weight: var(
      --sb-content-block-h4-font-weight,
      var(--sbt-font-weight-60)
    );
  }

  h5 {
    font-size: var(--sb-content-block-h5-font-size, var(--sbt-font-size-35));
    font-weight: var(
      --sb-content-block-h5-font-weight,
      var(--sbt-font-weight-60)
    );
  }

  h6 {
    font-size: var(--sb-content-block-h6-font-size, var(--sbt-font-size-25));
    font-weight: var(
      --sb-content-block-h6-font-weight,
      var(--sbt-font-weight-40)
    );
  }

  p {
    font-size: var(--sb-content-block-p-font-size, var(--sbt-font-size-30));
    line-height: var(
      --sb-content-block-p-line-height,
      var(--sbt-line-height-50)
    );
    margin: 1rem 0;
  }

  ul {
    list-style: disc;
    margin: 0 0 0 1.5rem;
    padding: 0;

    ul {
      margin: 0.25rem 0 0.25rem 1.5rem;
    }
  }

  li {
    font-size: var(--sb-content-block-li-font-size, var(--sbt-font-size-30));
    line-height: var(
      --sb-content-block-li-line-height,
      var(--sbt-line-height-50)
    );
    margin-bottom: 0.25rem;
  }
}
